"use client";
import DateTime from "@/app/components/forms/date_time/view/DateTime";
import Scanner from "@/app/components/forms/scanner/view/Scanner";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardFooter,
} from "@/components/ui/card";
import { useSingleSlotBooking } from "@/store/single_slot_booking_store/hooks/useSingleSlotBooking";
import React, { useState } from "react";

const stepsData = [
  {
    title: "New single-slot booking",
    component: <DateTime />,
    header: "Date and time",
    active: true,
    completed: true,
  },
  {
    title: "What exam skills are needed?",
    component: <Scanner />,
    header: "Scanner",
    active: false,
    completed: false,
  },
  {
    title: "What exam skills are needed?",
    component: <Scanner />,
    header: "Scanner 2",
    active: false,
    completed: false,
  },
  {
    title: "What exam skills are needed?",
    component: <Scanner />,
    header: "Comments 2",
    active: false,
    completed: false,
  },
];

const layout = () => {
  const { setSingleSlotBooking, singleSlotBooking } = useSingleSlotBooking();
  const [forms, setForms] = useState(stepsData);

  const handleNext = (currentIndex: number) => {
    setForms((prevForms) => {
      return prevForms.map((form, index) => {
        if (index === currentIndex) {
          return { ...form, active: false, completed: true };
        } else if (index === currentIndex + 1) {
          return { ...form, active: true, completed: false };
        }
        return form;
      });
    });
  };

  const handleBack = (currentIndex: number) => {
    setForms((prevForms) => {
      return prevForms.map((form, index) => {
        if (index === currentIndex) {
          return { ...form, active: false, completed: false };
        } else if (index === currentIndex - 1) {
          return { ...form, active: true, completed: true };
        }
        return form;
      });
    });
  };

  return (
    <div className="max-w-md mx-auto p-4 w-full">
      <div className="flex items-center gap-2 p-4">
        {forms.map(({ active, header, completed }, keys) => (
          <div
            className={`border-b-2 pb-1 ${active ? "border-b-primary" : ""}`}
            key={keys}
          >
            <p className="text-xs">
              {header} {!active && completed && "âœ…"}
            </p>
          </div>
        ))}
      </div>
      {forms.map(({ active, header, completed, component, title }, keys) => (
        <div key={keys}>
          {active && (
            <Card className="w-full">
              <CardHeader>
                <CardTitle className="text-start border-b-1 py-3">
                  {title}
                </CardTitle>
              </CardHeader>
              <CardContent className="w-full">{component}</CardContent>
              <CardFooter className="flex justify-between">
                <div>
                  {keys > 0 && (
                    <Button variant="outline" onClick={() => handleBack(keys)}>
                      Back
                    </Button>
                  )}
                </div>
                <div className="space-x-2">
                  <Button
                    onClick={() => handleNext(keys)}
                    disabled={!completed}
                  >
                    Next
                  </Button>
                </div>
              </CardFooter>
            </Card>
          )}
        </div>
      ))}
    </div>
  );
};

export default layout;
